import { html } from 'lit';
import { property, query } from 'lit/decorators.js';
import { watch } from '../../internal/watch';
import Icon from '../icon/icon.component';
import styles from './form-field-label.styles';
import Tooltip from '../tooltip/tooltip.component';
import UIKitElement from '../../internal/uikit-element';
import type { CSSResultGroup } from 'lit';

/**
 * @documentation https://kind-desert-0fb80550f.5.azurestaticapps.net
 *
 * @slot - The Form field label’s main content.
 * @slot tooltip - Use to display tooltip content for the form field label.
 * @slot required - Use to mark the form field label as required.
 *
 * @dependency koerber-icon
 *
 * @csspart base - The component's base wrapper.
 * @csspart label - The form field label container.
 */
export default class FormFieldLabel extends UIKitElement {
  static styles: CSSResultGroup = styles;
  static dependencies = { 'koerber-icon': Icon, 'koerber-tooltip': Tooltip };

  @query('slot:not([name])') defaultSlot: HTMLSlotElement;

  /** The label of the Form Field */
  @property() label = '';

  /** The tooltip of the Form field label */
  @property({ reflect: true }) tooltip = '';

  /** Makes the form field label a required field. */
  @property({ reflect: true, type: Boolean }) required = false;

  @property({ type: Boolean, reflect: true, attribute: 'accessible-hidden' }) accessibleHidden = false;

  @watch('required', { waitUntilFirstUpdate: true })
  handleRequiredChange() {
    this.required = !this.required;
  }

  render() {
    const infoIcon =
      this.tooltip.trim().length > 0
        ? html`
            <koerber-tooltip placement="top-start" hoist content=${this.tooltip}>
              <koerber-icon class="info_icon" size="medium" name="information"></koerber-icon>
            </koerber-tooltip>
          `
        : '';

    return html`${this.label.trim().length > 0
      ? html`
          <div class="body" aria-label=${this.label} ?aria-hidden=${this.accessibleHidden}>
            ${this.required ? html`<span class="asterisk">*</span>` : ''}
            <span class="label_container">${this.label}</span>
            ${infoIcon}
          </div>
        `
      : ''}`;
  }
}
